<template>
  <div id="app">
    <div id="nav">
      <router-link to="/">Home</router-link>
    </div>
    <router-view/>
    <section id ="app">

    <!-- The input -->
    <div class="query">
        <div class="wrapper" v-if="micro == false">
            <i class="material-icons iicon" @click="microphone(true)">mic</i>
            <input :aria-label="config.locale.strings.queryTitle"
              autocomplete="off"
              v-model="query"
              class="queryform"
              @keyup.enter="submit()"
              :placeholder="config.locale.strings.queryTitle"
              autofocus
              type="text">
            <i class="material-icons iicon t2s"
              @click="mute(true)"
              v-if="muted == false">volume_up</i>
            <i class="material-icons iicon t2s"
            @click="mute(false)"
            v-else>volume_off</i>
        </div>
        <div class="wrapper" v-else>
            <i class="material-icons iicon recording" @click="microphone(false)">mic</i>
            <input class="queryform" :placeholder="speech" readonly>
        </div>
    </div>
    <main class="wrapper ai-window">
        <br>
        <br>
        <!-- Display Welcome Message -->
        <div v-if="answers.length == 0 && online == true">
            <h1 class="title mdc-typography--headline">
                <div class="material-icons up">arrow_upward</div>
                <br>
                <br>
                  {{config.locale.strings.welcomeTitle}}
                  <p class="mdc-typography--body2">
                    {{config.locale.strings.welcomeDescription}}
                  </p>
            </h1>
        </div>

        <!-- Display offline message -->
        <div v-if="answers.length == 0 && online == false">
            <h1 class="title mdc-typography--headline">
                <div class="material-icons up">cloud_off</div>
                <br>
                <br>
                    {{config.locale.strings.offlineTitle}}
                    <p class="mdc-typography--body2">
                      {{config.locale.strings.offlineDescription}}
                    </p>
            </h1>
        </div>
        <!-- Chat window -->
        <!-- eslint-disable-next-line -->
        <table v-for="a in answers" class="chat-window">

            <!-- Your messages -->
            <tr>
                <td class="bubble">{{a.result.resolvedQuery}}</td>
            </tr>

            <!-- Dialogflow messages -->
            <tr>
                <td>

                    <!-- Bot message types / Speech -->

                    <div v-if="a.result.fulfillment.speech" class="bubble bot">
                        {{a.result.fulfillment.speech}}
                    </div>

                    <!-- Google Assistant output -->
                    <!-- eslint-disable-next-line -->
                    <div v-for="r in a.result.fulfillment.messages">

                        <!-- Bot message types / Card -->

                        <div class="mdc-card" v-if="r.type == 'basic_card'">
                            <img :title="r.image.accessibilityText"
                            :alt="r.image.accessibilityText"
                            class="mdc-card__media-item"
                            :src="r.image.url"
                            v-if="r.image">
                            <section class="mdc-card__primary">
                                <h1 class="mdc-card__title">{{r.title}}</h1>
                                <br>
                                <h2 class="mdc-card__subtitle">{{r.subtitle}}</h2>
                            </section>
                            <section class="mdc-card__supporting-text">
                                {{r.formattedText}}
                            </section>
                            <!-- eslint-disable-next-line -->
                            <section class="mdc-card__actions"
                              v-for="button in r.buttons">
                                <a class="mdc-button mdc-button--compact themed mdc-card__action"
                                  target="_blank"
                                  :href="button.openUrlAction.url">
                                    {{button.title}}
                                    <i class="material-icons openlink">open_in_new</i>
                                </a>
                            </section>
                        </div>

                        <!-- Bot message types / Carousel Card -->

                        <div class="slider" v-if="r.type == 'carousel_card'">
                            <carousel
                                    :perPage="1"
                                    :navigationEnabled="true"
                                    :paginationEnabled="false"
                                    navigationNextLabel="<button class='mdc-fab mdc-fab--mini material-icons rightnav'><span class='mdc-fab__icon'>keyboard_arrow_right</span></button>"
                                    navigationPrevLabel="<button class='mdc-fab mdc-fab--mini material-icons leftnav'><span class='mdc-fab__icon'>keyboard_arrow_left</span></button>"
                                    :navigationClickTargetSize="0"
                                    :loop="true">

                                <slide v-for="item in r.items" :key="item.id">
                                    <div class="mdc-card slide">
                                        <img class="mdc-card__media-item"
                                          :src="item.image.url"
                                          v-if="item.image">
                                        <section class="mdc-card__primary">
                                            <h1 class="mdc-card__title themed"
                                              @click="autosubmit(item.optionInfo.key)">
                                              {{item.title}}
                                            </h1>
                                        </section>
                                        <section class="mdc-card__supporting-text">
                                            {{item.description}}
                                        </section>
                                    </div>
                                </slide>
                            </carousel>
                        </div>

                        <!-- Bot message types / List -->

                        <div class="mdc-list-group mdc-card" v-if="r.type == 'list_card'">
                            <h3 class="mdc-list-group__subheader">{{r.title}}</h3>
                            <!-- eslint-disable-next-line -->
                            <ul class="mdc-list mdc-list--two-line mdc-list--avatar-list"
                              v-for="item in r.items"
                              @click="autosubmit(item.optionInfo.key)">
                                <li class="mdc-list-item">
                                    <img :title="item.image.accessibilityText"
                                      :alt="item.image.accessibilityText"
                                      class="mdc-list-item__start-detail"
                                      width="56" height="56"
                                      :src="item.image.url" v-if="item.image"/>
                                    <span class="mdc-list-item__text">
                                      {{item.title}}
                                      <span class="mdc-list-item__text__secondary">
                                        {{item.description}}
                                      </span>
                                    </span>
                                </li>
                            </ul>
                        </div>

                        <!-- Bot message types / Link Chip -->

                        <div v-if="r.type == 'link_out_chip'" class="chips">
                            <a class="suggestion link" :href="r.url" target="_blank">
                                {{r.destinationName}}
                                <i class="material-icons openlink">open_in_new</i>
                            </a>
                        </div>

                        <!-- Bot message types / Suggestion Chip -->

                        <div v-if="r.type == 'suggestion_chips'" class="chips">
                            <!-- eslint-disable-next-line -->
                            <div class="suggestion"
                              @click="autosubmit(s.title)"
                              v-for="s in r.suggestions">
                                {{s.title}}
                            </div>
                        </div>

                        <!-- Bot message types / Google Suggestion Chip -->

                        <!-- <div v-if="r.unknown == true" class="google-chip chips">
                            <a class="suggestion"
                              :href="'https://www.google.com/search?q=' + r.text"
                              target="_blank">
                                Search for "{{r.text}}" on Google
                                <i class="material-icons openlink">search</i>
                            </a>
                        </div> -->

                    </div>
                </td>
            </tr>
        </table>

        <br>
        <a id="bottom"></a>
    </main>
</section>
  </div>
</template>

<style lang="sass">
@import url('https://fonts.googleapis.com/css?family=Roboto')
@import "./assets/scss/App.sass"
</style>
<script>
import { ApiAiClient } from 'api-ai-javascript';
import config from './config';

// use the config token
const client = new ApiAiClient({ accessToken: config.app.token });

export default {
  name: 'app',
  data() {
    return {
      answers: [],
      query: '',
      speech: config.locale.strings.voiceTitle,
      micro: false,
      muted: config.app.muted,
      online: navigator.onLine,
      config,
    };
  },
  watch: {
    // eslint-disable-next-line
    answers(val) {
      setTimeout(() => {
        // if new answers arrive, wait for render and then
        // smoothly scroll down to #bottom selector, used as anchor
        document.querySelector('#bottom').scrollIntoView({
          behavior: 'smooth',
        });
      }, 2);
    },
  },
  methods: {
    submit() {
      client.textRequest(this.query).then((response) => {
        if (response.result.action === 'input.unknown' && this.config.app.googleIt === true) {
          response.result.fulfillment.messages[0].unknown = true;
          response.result.fulfillment.messages[0].text = response.result.resolvedQuery;
        } // if the googleIt is enabled, show the button

        this.answers.push(response);
        this.handle(response); // <- handle the response in handle() method

        this.query = '';
        this.speech = config.locale.strings.voiceTitle; // <- reset query and speech
      });
    },
    handle(response) {
      if (response.result.fulfillment.speech || response.result.fulfillment.messages[0].type === 'simple_response') {
        let speechText = response.result.fulfillment.speech;
        if (speechText === null || speechText === '') {
          speechText = response.result.fulfillment.messages[0].textToSpeech;
        }
        const speech = new SpeechSynthesisUtterance(speechText);

        speech.voiceURI = 'native';
        speech.lang = config.locale.settings.speechLang;
        // Speech output if microphone is allowed
        if (this.muted === false) window.speechSynthesis.speak(speech);
      }
    },
    autosubmit(suggestion) {
      this.query = suggestion;
      this.submit();
    },
    mute(mode) {
      this.muted = mode;
    },
    microphone(mode) {
      this.micro = mode;
      const self = this; // <- correct scope

      if (mode === true) {
        // chrome speech recognition
        // eslint-disable-next-line
        const recognition = new webkitSpeechRecognition();

        recognition.interimResults = true;
        recognition.lang = config.locale.settings.recognitionLang;
        recognition.start();

        // eslint-disable-next-line
        recognition.onresult = function (event) {
          for (let i = event.resultIndex; i < event.results.length; i += 1) {
            self.speech = event.results[i][0].transcript;
          }
        };

        // eslint-disable-next-line
        recognition.onend = function () {
          recognition.stop();
          self.micro = false;
          self.autosubmit(self.speech);
        };
      }
    },
  },
};
</script>
